<script>
import { Bar } from "vue-chartjs/es/BaseCharts";

export default {
  extends: Bar,
  props: ["data"],
  data() {
    return {
      options: {
        legend: {
          display: false,
        },
        scales: {
          xAxes: [
            {
              ticks: {
                fontColor: "white",
                fontSize: "16",
                callback: (value) => {
                  if (
                    value != "03:00" &&
                    value != "09:00" &&
                    value != "15:00" &&
                    value != "21:00"
                  ) {
                    return value;
                  }
                },
              },
              gridLines: {
                color: "grey",
                lineWidth: 1,
              },
            },
          ],
          yAxes: [
            {
              ticks: { fontColor: "white", fontSize: "14" },
              gridLines: {
                color: "grey",
                lineWidth: 1,
              },
            },
          ],
        },

        responsive: true,
        maintainAspectRatio: false,
      },
    };
  },

  mounted() {
    this.renderChart(this.data, this.options);
  },

  watch: {
    data: function () {
      if (this.data.length != 0) {
        const minimum = 0;
        const maxFromData = Math.max.apply(null, this.data.datasets[0].data);
        const percent = (maxFromData * 10) / 100;
        const maximum = Math.ceil(maxFromData + percent);
        /*    const maximum =
        Math.ceil(Math.max.apply(null, this.data.datasets[0].data) / 5) * 5; */
        this.options.scales.yAxes[0].ticks.min = minimum;
        this.options.scales.yAxes[0].ticks.max = maximum;
        this.renderChart(this.data, this.options);
      }
    },
  },
};
</script>